import Box from "@material-ui/core/Box";
import React from "react";
import {RootState} from "./reducers";
import {connect, ConnectedProps} from "react-redux";

class NaglowekInternal extends React.Component<PropsFromRedux> {
    render() {
        const { logo_url, dane_gminy } = this.props
        const { gmina, typ_gminy, ludnosc } = dane_gminy;

        return <Box className="wstep" style={{backgroundImage: "url(" + logo_url + ")"}}>
            <Box className="prawa">
                <Box>&nbsp;</Box>
                <Box className="nazwa_gminy"><strong>{gmina}</strong></Box>
                <Box>{typ_gminy?.replace(gmina || 'ble', '')}</Box>
                <Box><strong>{ludnosc}</strong>&nbsp;{(gmina !== '')?"mieszkańców":""}</Box>
                <Box>&nbsp;</Box>
                <Box>&nbsp;</Box>
            </Box>
        </Box>
    }
}

const mapStateToProps = (state: RootState) => {
    return {
        dane_gminy: state.dane_gminy,
        logo_url: state.logo_url,
    };
};

const connector = connect(
    mapStateToProps
);

type PropsFromRedux = ConnectedProps<typeof connector>

export default connector(NaglowekInternal);