import React from "react";
import ReactJoyride from "react-joyride";

interface Props {
    enabled: boolean;
    finishedCallback: any;
}

export class Tour extends React.Component<Props> {
    static STEPS = [
        {
            target: '.prawa',
            content: 'Generator pozwala Ci stworzyć grafikę do publikacji na FB z danymi z dowolnej gminy',
            disableBeacon: true,
        },
        {
            target: '.acc-ustawienia',
            content: 'Tutaj możesz ustawić logo (podaj link do publicznie dostępnej grafiki), wybrać województwo i gminę a na koniec pobrać grafikę lub pobrać link aby podzielić się projektem z innymi',
        },
        {
            target: '.acc-komentarz',
            content: 'Tutaj dodasz komentarz swój, polityka lub eksperta na temat którego dotyczy infografika',
        },
        {
            target: '.acc-budzet',
            content: 'Tutaj możesz włączyć lub wyłączyć pokazywanie modułów dotyczące budżetu danej gminy oraz ustawić ich opcje',
        },
        {
            target: '.acc-wykresy',
            content: 'A tutaj możesz dodać lub usunąć wykres z danymi z GUS, ale też oparty na własnych danych',
        },
        {
            target: '.start-tour',
            content: 'A tutaj ponownie uruchomisz samouczek',
        },
    ]

    render () {
        const { enabled, finishedCallback } = this.props;
        // @ts-ignore
        const callback = data => {
            const { action } = data;
            if (action === "reset" || action === "close") {
                finishedCallback();
            }
            // TODO: Capture close, skip, next / prev action
        };
        return (
            <div className="app">
                <ReactJoyride run={enabled}
                              steps={Tour.STEPS} continuous={true} showSkipButton={true} showProgress={true}
                              callback={callback}
                              styles={{
                                  options: {
                                      primaryColor: '#f9c00c',
                                  },
                                  tooltipContainer: {
                                      textAlign: "justify",
                                  }
                              }}

                              locale={{
                                  next: "Dalej",
                                  last: "Zamknij",
                                  skip: "Pomiń",
                                  back: "Wstecz"
                              }}
                />
            </div>)
    }

}